<template>
    <div class="page-content">
        <el-row class="input-group">
            <el-col :span='6' class="input-label">
                <span></span>
            </el-col>
            <el-col :span='12' class="input-control">
                <el-input v-model="name" placeholder="" size="small"></el-input>
            </el-col>
        </el-row>
        <el-row class="input-group">
            <el-col :span='6' class="input-label">
                <span></span>
            </el-col>
            <el-col :span='12' class="input-control">
                <el-input v-model="des" placeholder="" size="small"></el-input>
            </el-col>
        </el-row>
        <el-row class="input-group">
            <el-col :span='6' class="input-label">
                <span></span>
            </el-col>
            <el-col :span='12' class="input-control">
                <el-radio-group v-model="inputType" size="small">
                    <el-radio label="text"></el-radio>
                    <el-radio label="textarea"></el-radio>
                    <el-radio label="bolean">(Y/N)</el-radio>
                    <el-radio label="number"></el-radio>
                </el-radio-group>
            </el-col>
        </el-row>
        <el-row class="input-group">
            <el-col :span='6' class="input-label">
                <span></span>
            </el-col>
            <el-col :span='12' class="input-control">
                <el-input v-model="value" placeholder="" size="small"></el-input>
            </el-col>
        </el-row>
        <el-row class="input-group">
            <el-col :span='6' class="input-label">
                <span></span>
            </el-col>
            <el-col :span='12' class="input-control">
                <el-select v-model="group" placeholder="" size="small">
                    <el-option :value="1" label=""></el-option>
                    <el-option :value="2" label=""></el-option>
                    <el-option :value="3" label=""></el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row class="input-group text-center">
            <el-button type="primary" size="small" :loading='loading' @click="save"></el-button>
            <el-button type="primary" size="small" @click="reset"></el-button>
        </el-row>
    </div>
</template>
<script>
export default {
  data: function() {
    return {
      name: "",
      des: "",
      inputType: "",
      value: "",
      group: "",
      loading: false
    };
  },
  methods: {
    save: function() {
      var _this = this;
      _this.loading = true;
      _this.axios
        .post("/system/base/add")
        .then(res => {
          _this.loading = false;
          _this.$message({
            message: "",
            type: "success"
          });
        })
        .catch(err => {
          _this.loading = false;
          _this.$message({
            message: "",
            type: "error"
          });
        });
    },
    reset: function() {
      this.name = "";
      this.des = "";
      this.inputType = "";
      this.value = "";
      this.group = "";
    }
  }
};
</script>
<style lang="less" scoped>
.page-content {
  width: 100%;
  min-width: 800px;
  height: 100%;
  padding: 20px 10px;

  .text-center {
    text-align: center;
  }
}
</style>


