// system router
import System from '../pages/system/index.vue'
import MenuBar from '../pages/system/menubar.vue'
import Base from '../pages/system/base.vue'
import User from '../pages/system/user.vue'
import UserModel from '../pages/system/usermodel.vue'
import Db from '../pages/system/db.vue'
import Log from '../pages/system/log.vue'


export default {
    path: '/system',
    meta: { title: '' },
    component: System,
    children: [
        { path: '', name: 'system', meta: { title: '' }, component: MenuBar },
        { path: 'base', name: 'system-base', meta: { title: '' }, component: Base },
        { path: 'user', name: 'system-user', meta: { title: '' }, component: User },
        { path: 'usermodel', name: 'system-usermodel', meta: { title: '' }, component: UserModel },
        { path: 'db', name: 'system-db', meta: { title: '' }, component: Db },
        { path: 'log', name: 'system-log', meta: { title: '' }, component: Log }
    ]
}


